import React, { useState, useEffect } from 'react'
import { Link as RouterLink } from 'react-router-dom'

import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { logUserOut } from '../Store/userActions';

import { makeStyles, withStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import { Toolbar, Typography, Button, Link } from '@material-ui/core'
import Badge from '@material-ui/core/Badge';
import { withRouter } from 'react-router'
import './stylez.scss'

const useStyles = makeStyles(theme => ({
    toolbarTitle: {
        flex: 1,
    },
    toolbarSecondary: {
        justifyContent: 'space-between',
        overflowX: 'auto',
        backgroundColor: '#0398c1',
        color: 'white'
    },

    toolbarLink: {
        padding: '12px 55px 13px 55px',
        flexShrink: 0,
    }
}));

const sections = [
    { title: 'Pgina Inicial', link: '/' },
    { title: 'Consultas', link: '/consultas' },
    { title: 'Veculos', link: '/veiculos' },
    { title: 'Empresas', link: '/empresas' },
    { title: 'Relatrios', link: '/relatorios' },
    { title: 'Solicitaes ', link: '/solicitacoes' },
    { title: 'Editar Conta', link: '/editAccount', icon: 'person' },
    { title: 'Avisos', link: '/avisos', icon: 'notifications' },
]

const adminSections = [
    { title: 'Alterar parmetros do sistema', link: '/parametros', icon: 'settings' },
    { title: 'Gesto de usurios', link: '/users', icon: 'group' },
]

const StyledBadge = withStyles((theme) => ({
    badge: {
        right: -8,
        top: -1,
        padding: '0 4px',
    },
}))(Badge);

const StyledBadge2 = withStyles((theme) => ({
    badge: {
        right: -2,
        top: 2.5,
        padding: '0',
    },
}))(Badge);


const Header = props => {

    const
        logs = props?.logs,
        { parametros, avisos } = props,
        { pathname } = props.location,
        { user } = props,
        demand = localStorage.getItem('demand'),
        classes = useStyles(),
        { toolbarTitle, toolbarSecondary, toolbarLink } = classes

    const
        [path, setSelected] = useState(document.location.pathname)
        , [logCounter, setLogCounter] = useState()
        , [avisosCounter, setAvisosCounter] = useState()

    //Atualiza o menu superior, destacando a aba selecionada e atualizando o nmero de solicitaes em aberto
    useEffect(() => {
        if (logs && Array.isArray(logs)) {
            const count = logs
                .filter(l => l?.completed === false)
                .length

            setLogCounter(count)
        }
        setSelected(pathname)
    }, [pathname, logCounter, logs])

    //Atualiza o menu superior com o nmero de avisos no lidos
    useEffect(() => {
        if (Array.isArray(avisos)) {

            const
                { messagesRead } = user
                , count = avisos
                    .filter(aviso => {
                        if (!messagesRead)
                            return true
                        else
                            return !messagesRead.includes(aviso?.id)
                    }
                    )
                    .length

            setAvisosCounter(count)
        }
        //setSelected(pathname)
    }, [setAvisosCounter, avisos, user])


    const selected = link => {
        let
            style = document.querySelector("a[href='/solicitacoes']")?.style,
            bgColor = '',
            borderB = '',
            borderT = '',
            fontW = '400'

        if (path === '/solicitacoes' || demand) {
            if (style) {
                style['background-color'] = '#11a7d2'
                style['border-bottom'] = '1.5px solid #ccc'
            }
        }

        else if (path.match(link) && link !== '/') {
            bgColor = '#11a7d2'
            borderB = '1.5px solid #ccc'
            if (style) {
                style['background-color'] = ''
                style['border-bottom'] = ''
            }
        }
        return { bgColor, borderB, borderT, fontW }
    }
    //Pega o nome e a sigla da Secretaria do DB, editveis na opo "Parmetros" do sistema.
    let
        nomeSecretaria = '',
        siglaSecretaria = ''

    if (parametros && parametros[0]) {
        const { nomes } = parametros[0]
        nomeSecretaria = nomes.secretaria
        siglaSecretaria = nomes.siglaSecretaria
    }

    const logout = async () => props.logUserOut(user?.socketId)

    return (
        <React.Fragment>
            <CssBaseline />

            <Toolbar>
                <Typography
                    component="h2"
                    variant="h6"
                    color="inherit"
                    align="center"
                    noWrap
                    className={toolbarTitle}
                >
                    {siglaSecretaria} - {nomeSecretaria}
                </Typography>
                <Button variant="outlined" size="small" onClick={() => logout()}>
                    Sair
                </Button>
            </Toolbar>
            <Toolbar component="nav" variant="dense" className={toolbarSecondary}>
                {sections.map(({ link, title, icon }, i) => (
                    !icon &&
                    <Link
                        component={RouterLink}
                        to={link}
                        key={i}
                        color="inherit"
                        noWrap
                        variant="body2"
                        href=""
                        style={{
                            fontFamily: 'Segoe UI, Tahoma, Geneva, Verdana, sans-serif',
                            fontSize: '15px',
                            backgroundColor: selected(link).bgColor,
                            borderBottom: selected(link).borderB,
                            fontWeight: selected(link).fontW,
                            borderTop: selected(link).borderT
                        }}
                        className={toolbarLink}
                        onClick={() => setSelected(link)}
                    >
                        {
                            title === 'Solicitaes ' ?

                                <StyledBadge badgeContent={logCounter} color='secondary'>
                                    <span>
                                        {title}
                                    </span>
                                </StyledBadge>
                                :
                                title
                        }
                    </Link>
                ))}
                {
                    user.role === 'admin' ?                     //Apenas usurios admin podem visualizar essas opes
                        adminSections.map(({ title, link, icon }, i) =>
                            <Link component={RouterLink} to={link} key={i}>
                                <span
                                    className="material-icons adminLink"
                                    style={{ color: 'white', cursor: 'pointer' }}
                                    title={title}
                                >
                                    {icon}
                                </span>
                            </Link>
                        )
                        :
                        <Link component={RouterLink} to={sections[6].link}>
                            <span
                                className="material-icons iconHeaderLink"
                                style={{ color: 'white', cursor: 'pointer' }}
                                title={sections[6].title}
                            >
                                {sections[6].icon}
                            </span>
                        </Link>
                }
                <Link component={RouterLink} to={sections[7].link}>
                    <StyledBadge2 badgeContent={avisosCounter} color='secondary'>
                        <span
                            className="material-icons avisosLink"
                            style={{ color: 'white', cursor: 'pointer' }}
                            title={sections[7].title}
                        >
                            {sections[7].icon}
                        </span>
                    </StyledBadge2>
                </Link>

            </Toolbar>
        </React.Fragment >
    )
}

function mapStateToProps(state) {
    return {
        logs: state.data?.logs,
        parametros: state.data?.parametros,
        avisos: state.data?.avisos,
        user: state.user
    }
}

function mapDispatchToProps(dispatch) {
    return bindActionCreators({ logUserOut }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Header))