import { cnpj, phone, email } from './commonFields'

export const empresasForm = [
    {
        field: 'razaoSocial',
        label: 'Razo Social'
    },
    {
        ...cnpj,
        maxLength: 18,
        //pattern: /^\d{2}\.\d{3}\.\d{3}\/\d{4}-\d{2}$/
        pattern: '[0-9]{2}.?[0-9]{3}.?[0-9]{3}/?[0-9]{4}-?[0-9]{2}'
    },
    {
        field: 'inscricaoEstadual',
        label: 'Inscrio Estadual',
        maxLength: 13,
        pattern: '\\d{13}'
    },
    {
        ...phone,
        field: 'telefone',
        label: 'Telefone',
    },
    {
        ...email,
        field: 'email',
        label: 'E-mail'
    },
    {
        field: 'cep',
        label: 'CEP',
        maxLength: 10
    },
    {
        field: 'rua',
        label: 'Rua',
        maxLength: 100,
    },
    {
        field: 'numero',
        label: 'Nmero',
        type: 'number',
        max: 32000
    },
    {
        field: 'complemento',
        label: 'Complemento',
        maxLength: 20,
        notRequired: true
    },
    {
        field: 'bairro',
        label: 'Bairro',
        maxLength: 40,
    },
    {
        field: 'cidade',
        label: 'Cidade',
        maxLength: 60,
    },
    {
        field: 'uf',
        label: 'Estado',
        maxLength: 2,
        pattern: '[A-Z]{2}'
    },
    {
        field: 'numeroContrato',
        label: 'Nmero do Contrato Social',
    },
    {
        field: 'vencimentoContrato',
        label: 'Vencimento do Cadastro - CRC',
        type: 'date',
    }
]