const avisosTable = [
    {
        field: 'from',
        title: 'Remetente',
        label: 'Remetente',
        render: rowData => rowData.from ? rowData.from : 'Aviso automtico do sistema'
    },
    {
        field: 'vocativo',
        title: 'Destinatrio',
        label: 'Destinatrio',
    },
    {
        field: 'subject',
        title: 'Assunto',
        label: 'Assunto',
    },
    {
        field: 'read',
        title: 'Lida',
        label: 'Status',
        hidden: true
    },
    {
        field: 'createdAt',
        title: 'Data de criao do aviso',
        label: 'Data de criao do aviso',
        type: 'date',
        format: value => value ? value : 'Nenhuma data informada'
    },
]

export default avisosTable