import humps from 'humps'
const validaDistanciaMinima = (utilizacao, dist, distancias) => {

    utilizacao = humps.camelize(utilizacao)
    if (utilizacao === 'leitoEExecutivo')
        utilizacao = 'leitoExecutivo'
    let validDistance, d

    if (distancias[utilizacao] && dist && dist !== '') {
        dist = Number(dist)
        d = distancias[utilizacao]

        if (d <= dist)
            validDistance = true
        else
            validDistance = false
    }
    if (validDistance)
        return false
    else if (d)
        return `A distncia mnima para a utilizao "${utilizacao}"  de ${d} centmetros. Altere a distncia ou clique em "voltar" para alterar a utilizao.`
}

export default validaDistanciaMinima